.Header{
  border: 1px solid;
  padding: 0 15px;
  margin: 10px 15px;
  color: #fff;
  background: tomato;
  overflow: auto;
}

.BodyContainerMain {
  padding: 0 15px;
  margin: 10px 15px;
  overflow: auto;
}

ul.Nav{
  list-style-type: none !important;
  margin: 0; padding: 0;
  font-weight: bold;
  float: right;
}
.Nav li{
  float: left;
  margin-right: 10px;
  cursor: pointer;
  color:#000;
  padding: 5px 10px;
}


.Nav li:hover{
  background: #ff7547;
  color: #fff;;
}

li .active{
  color: wheat;
}

.loadingIcon{
  display: block;
  width: 200px;
  margin: 10px auto;
}

.BodyContainer{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;

}

.bookTile{
  flex-basis: 18%;
  border: 1px solid #d0d0c1;
  padding: 7px;
  margin: 10px 0;
  position: relative;
}

@media screen and (max-width: 1000px){
  .bookTile{
    flex-basis: 45%;
    border: 1px solid #d0d0c1;
    padding: 7px;
    margin: 10px 0;
    position: relative;
  }
}

@media screen and (max-width: 500px){
  .bookTile{
    flex-basis: 90%;
    border: 1px solid #d0d0c1;
    padding: 7px;
    margin: 10px 0;
    position: relative;
  }
}

.imgContainer{
  display: block;
  width: 80%;
  margin: 7px auto;
  height: 150px;
  position: relative;
  overflow: hidden;
}
.bookImage{
  position: absolute;
  width:100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
}

h2, h3, h4, h5, p{
  margin: 5px;
  padding: 0;
  font-weight: 400;
}

h4{font-weight: 600;}

.buyButton{
  display: block;
  margin: 5px auto;
  text-align: center;
  background: #86bc25;
  box-shadow: 1px -2px 4px 2px #777777;
  border-radius: 10px;
  margin: 5px;
  padding: 0 15px 5px 15px;
  position: absolute;
  bottom: 5px;
  left: 50%;
  transform: translateX(-50%);
  cursor: pointer;
}
.buyButton:hover{color: #fff; background-color: #1a485e;}

.addtocart{
  display: block;
  width:100px;
  margin: 10px auto;
  text-align: center;
  background: #86bc25;
  box-shadow: 1px -2px 4px 2px #777777;
  border-radius: 10px;
  padding: 5px 15px 10px 15px;
  cursor: pointer;
  font-weight: 500;
}

.addtocart:hover{color: #fff; background-color: #1a485e;}

.bookDtl{
  width: 100px;
  box-shadow: 1px 2px 4px 1px #777777;
  margin: 10px;
  float: left;
}

.fltLeft{margin-top: 7px;}

.priceTag{position: absolute; right: 10px; bottom: 5px;}
.cartItem{
  position: relative;
  border: 1px solid #d0d0c1;
  padding: 5px 15px;
  margin: 5px 20px;
  background: #f3f3f3;
  border-radius: 5px;
}
.cartItem span{margin: 5px 10px;}